import { Component, OnInit } from '@angular/core';
import { MenuService } from '../../../../../auth/_services/menu.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';

@Component({
    selector: 'app-menu-edit',
    templateUrl: "menu-edit.component.html",
    styles: []
})
export class MenuEditComponent implements OnInit {
    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private route: ActivatedRoute, private router: Router, private _menuService: MenuService, private formBuilder: FormBuilder) { }
    public menuData = '';
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    public selectedStatus = '';

    menu_id = '';
    menu_name = '';

    p: number = 1;

    ngOnInit() {
        /*   localStorage.setItem('section', 'menu');
     
           this._menuService.getAll().subscribe(
               data => {
                   this.menuData = data.success;
                 
               }
           );
    */


        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.product_menu != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        this.route.params.subscribe(
            params => {

                console.log('params', params);
                if (params.menu_id) {

                    this._menuService.getById(params.menu_id).subscribe(
                        data => {

                            this.menu_id = data.success.id;
                            this.menu_name = data.success.menu_name;
                            this.selectedStatus = data.success.status;

                        }
                    );
                }
            }
        );


        this.form = this.formBuilder.group({
            menu_id: [null],
            menu_name: [null],
            status: [null]
        });


    }


    isFieldValid(field: string) {
        return (
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
            /*
            (!this.form.get(field).valid && !this.formSumitAttempt) ||
            (!this.form.get(field).valid && this.form.get(field).touched && this.formSumitAttempt)*/
        );
    }

    displayFieldCss(field: string) {


        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };


    }

    onSubmit() {
        this.formSumitAttempt = true;
        //  if (this.form.valid) {
        let that = this;
        this._menuService.updateMenu({ form_data: this.form.value }).subscribe(
            data => {
                //this.menuData = data.success;
                console.log(data);
                this.menuData = data.data;
                this.toastr.success('Menu Update Successfully', 'Success Message');
                this.reset();

                this.router.navigate(['category/menu-list']);
            }
        );
        //}
    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }

}
